:root {
    --Text-Primary: #FFF;
    --Text-Secondry: rgba(255, 255, 255, 0.75);
    --Text-Warning: #ED8C20;
    --Text-Hint: rgba(255, 255, 255, 0.25);
    --Border-Default: rgba(255, 255, 255, 0.10);
}

.order-add-cart-block {
    width: 100%;
    border-radius: 10px;
    border: 1px solid var(--Border-Default);
    background: #262328;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    color: var(--Text-Primary);
    margin-top: 20px;
    font-family: 'TTFirsNeue Regular';
    font-style: normal;
    font-weight: 400;
    padding: 20px 20px 10px 20px;
}

form {
    width: 100%;
}

form > a {
    cursor: pointer;
    width: 100%;
    height: 56px;
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 0;
    background-color: black;
    border-radius: 8px;

}

form > a > img {
    width: 100%;
    height: 36px;
}


.add-cart-block {
    width: 100%;
}

.label-text {
    width: 100%;
    background-color: #ED8C201A;
    border-radius: 4px;
    text-align: center;
    height: 28px;
    margin-bottom: 10px;
    margin-top: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}


.label-text p {
    color: var(--Text-Warning);
    font-size: 12px;
    font-weight: 400;
}

.card-number-container {
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    position: relative;
}

.card-number-container::after {
    content: attr(data-card-type);
    position: absolute;
    top: 50%;
    right: 10px;
    transform: translateY(-50%);
    padding: 0 10px;
    background-color: #fff;
    border: 1px solid #ccc;
    border-radius: 5px;
    color: #000;
}

input[type=text], input[type="number"], input[type="password"] {
    width: 100%;
    height: 56px;
    padding: 20px;
    border-radius: 8px;
    border: 1px solid var(--Border-Default);
    background: #211F22;
    color: var(--Text-Primary);
    font-size: 16px;
}

input.errorFocused {
    border: none;
}

input[type=text]:focus,
input[type=number]:focus,
input[type=password]:focus {
    outline: none;
    border-color: #E1FF00;
    box-shadow: 0 0 0 2px #E1FF00;
}


input.error {
    border: 1px solid red;
}

input.error:focus {
    border-color: #E1FF00;
}
.cvv-container {
    width: 100%;
    display: flex;
    justify-content: space-between;
    margin: 10px 0;
    position: relative;
}


.cvv-container input:first-child{
    width: 100%;
    margin-right: 5px;
}
.cvv-container img{
    cursor: pointer;
}
.cvv-input{
    width: 100%;
    margin-left: 5px;

}

.cvv-input img{
    position: absolute;
    right: 20px;
    bottom: 15px;
}

input[type="submit"] {
    width: 100%;
    height: 56px;
    color: var(--Text-Primary);
    border: none;
    padding: 15px 20px;
    border-radius: 8px;
    background: #8B53E8;
    cursor: pointer;
    font-size: 16px;
    transition: background-color 200ms ease-in-out;
}

input[type="submit"]:hover {
    background-color: #7346BB;
}

.submit-button {
    width: 100%;
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
    margin-top: 20px;
}

.accept-text {
    width: 100%;
    text-align: center;
    margin-bottom: 0;
}

.accept-text p {
    color: var(--Text-Hint);
    font-size: 12px;
    margin-bottom: 0;
    margin-top: 0;
    line-height: 16px;
}

.accept-text a {
    text-decoration: none;
    color: var(--Text-Secondry);
    font-size: 12px;
    margin-bottom: 0;
    margin-top: 0;

}

@media screen and (max-width: 375px) {
    .cvv-container input:first-child{
        width: 100%;
        margin-right: 10px;
    }
    .cvv-input{
        width: 100%;
        margin-left: 10px;

    }
}

/* Loader styles */

.loader-container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.loader {
    width: 10px;
    height: 10px;
    border: 2px solid #fff;
    border-radius: 50%;
    animation: loaderAnimation 1s infinite ease-in-out;
    margin: 0 5px;
}

@keyframes loaderAnimation {
    0% {
        transform: scale(0.5);
        opacity: 0.5;
    }
    50% {
        transform: scale(1.5);
        opacity: 1;
    }
    100% {
        transform: scale(0.5);
        opacity: 0.5;
    }
}
